body {
  --blue-color: #00acdc;
  margin: 0;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
}

ul, li, a {
  margin: 0;
  padding: 0;
}

ul {
  list-style: none;
}

a {
  color: inherit;
  text-decoration: none;
}

.header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.header-logo-link {
  width: 40px;
  height: 40px;
}

.header-logo {
  width: 100%;
  height: 100%;
}

.nav-list {
  display: flex;
}

.nav-list > a:not(:last-child) {
  margin-right: 20px;
}

.nav-link {
  align-items: center;
  height: 60px;
  display: flex;
}

.nav-link:hover {
  color: var(--blue-color);
}

.is-active {
  color: var(--blue-color);
  position: relative;
}

.is-active:after {
  content: "";
  background-color: var(--blue-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}

.nav-item {
  text-transform: uppercase;
}
